@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper{
    padding-top: 10%;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100vh;

    background: rgba(45,176,224,1);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(45,176,224,1)), color-stop(100%, rgba(96,40,168,1)));
    background: linear-gradient(45deg, rgba(45,176,224,1) 0%, rgba(96,40,168,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2db0e0', endColorstr='#6028a8', GradientType=1 );
}

.main {
    height: 55vh;
    background: #2a6bb1;
    box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
    width: 60%;
    margin: 0 auto;
}

.h3{

    padding-left: 75px;
    padding-top: 38px;
    font-size: 49px;
    color: #fff;
    letter-spacing: 8px;
}

.todolist_right{
    max-width: 500px;
    margin: 0 auto;
    margin-top: 50px;
    padding: 30px;
    border: #fff solid 1px;
    border-radius: 10px;
}

.todolist_right .task{
    padding: 5px;
    margin-top: 5px;
}

.todolist_right .task:hover{
    background-color: #fff;
}

.todolist_right .task .delete {
    cursor: pointer;
    float: right;
    color: red;
    display: none;
}
.todolist_right .task:hover .delete {
display: inline-block;
}
.todolist_right .task.done {
    text-decoration: line-through;
}

.input {
    width: 100%;
    height: 35px;
}

.row {
    display: flex;
}

.task_text {
font-size: 20px;
}

body {
    font-family: "Open Sans", serif;
}

.todolist_footer {
    display: flex;
    justify-content: space-between;
}

button {
    border: 2px solid #103a65;
    border-bottom: 4px solid #103a65;
    padding: 8px 20px;
    margin: 0 2px;
    border-radius: 2px;
    cursor: pointer;
    background-color: #235d9c;
    color: #fff;
    outline: none!important;
}


button:hover {
    border-bottom: 2px solid #103a65;
    position: relative;
    top: 2px;
}

button:active {
    border-bottom: 2px solid transparent;
    border-color: transparent;
    position: relative;
    top: 2px;
}
.btn{
    padding-top: 10px;
    color: #fff;
}
.btn_clear {
    cursor: pointer;
    
}
.item_style{
    padding-right: 10px;
    padding-top: 10px;
    color: #fff;
}
